import React, { Fragment } from 'react';
import Services from './service';
import './component.css';

import poko from '../assets/manual1.png'
import cromados from '../assets/cromados.png'
import poko2 from '../assets/manual2.png'

function Projects(){

    return(
        <Fragment>
            <h2 className="display-3" id="projects">Projects</h2>
            <div className="bg-project mb-5">

                <div className="container">
                    <Services />

                <div className="mt-5 row" id="gallery">

                    <div className="col my-2">                    
                        <img className=" img-demo shadow" src={cromados} alt="..."/>
                        <br />
                        <div className="carousel-caption d-md-block">
                        <h4 > <a href="https://cromadosbarranquilla.com.co" className="txt" target="_Blank">Cromados Barranquilla</a> </h4>
                        <p id="description">Angular JS - in production</p>
                        </div>
                    </div>

                    <div className="col my-2">                    
                        <img className=" img-demo shadow" src={poko2} alt="..."/>
                        <br />
                        <div className="carousel-caption d-md-block">
                        <h4 ><a href="#" className="txt" id="notYet">Poko Sketch</a> </h4>
                        <p id="description"> React JS - under construction</p>
                        </div>
                    </div>

                    <div className="col my-2">                    
                        <img className="img-demo shadow" src={poko} alt="..."/>
                        <br />
                        <div className="carousel-caption d-md-block">
                        <h4 > <a href="#" className="txt" id="notYet">Palmera Azul</a> </h4>
                        <p id="description"> React JS - under construction</p>
                        </div>
                    </div>

                </div>


                </div>
            </div>
        </Fragment>
    )
}

export default Projects;