import React, { Fragment } from 'react';
import sergio from '../assets/sergio.jpg';
import './component.css';
import Skills from './skills';

function Bio(){

    return(

        <Fragment>
            <div className="container mt-5 mb-5" id="bio">
                <h2 className="display-4">About me</h2>
            <div className="row mx-4">

                <div className="col">

                    <img className="rounded-circle bio mb-4"
                    id="sergio" src={sergio} alt="sergio_campbell_developer"/>

                    <p className="container bio">
                    Since 2010 start my journey as freelance developer I've done formal work for some companies 
                    in Colombia, then in 2020 begin to do it full remote. I collaborated with talent people
                    in telecommunications sector building and supporting ADSL installations and of course
                    development and design some internal interfaces for business.
                    <br /><br/>
                    I'm adventour, curious and like to work and collaborate with awesome people and 
                    improve my little details step by step in each project.
                    </p>
                </div>

                <div className="col">
                    <Skills />
                </div>

            </div>
            </div>
        </Fragment>
    

    )
}

export default Bio;